import React, { useState, useEffect } from "react";
import "./clock.css";

function Clock() {
  const [days, setDays] = useState();
  const [hours, setHours] = useState();
  const [minutes, setMinutes] = useState();
  const [seconds, setSeconds] = useState();

  let interval;

  const countDown = () => {
    const destination = new Date("Sep 30, 2024").getTime();
    interval = setInterval(() => {
      const now = new Date().getTime();
      const different = destination - now;
      const days = Math.floor(different / (1000 * 60 * 60 * 24));
      const hours = Math.floor(
        (different % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      );
      const minutes = Math.floor((different % (1000 * 60 * 60)) / (1000 * 60));
      const secounds = Math.floor((different % (1000 * 60)) / 1000);

      if (destination < 0) clearInterval(interval.current);
      else {
        setDays(days);
        setHours(hours);
        setMinutes(minutes);
        setSeconds(secounds);
      }
    });
  };

  useEffect(() => {
    countDown();
  });

  return (
    <div className="clock__wrapper d-flex align-item-center gap-4">
      <div className="clock__data d-flex align-item-center gap-4">
        <div className="text-center">
          <h1 className="text-white mb-2">{days}</h1>
          <h5 className="text-white">Days</h5>
        </div>
        <span className="text-white fs-3">:</span>
      </div>

      <div className="clock__data d-flex align-item-center gap-4">
        <div className="text-center">
          <h1 className="text-white mb-2">{hours}</h1>
          <h5 className="text-white">Hours</h5>
        </div>
        <span className="text-white">:</span>
      </div>

      <div className="clock__data d-flex align-item-center gap-4">
        <div className="text-center">
          <h1 className="text-white mb-2">{minutes}</h1>
          <h5 className="text-white">Minutes</h5>
        </div>
        <span className="text-white">:</span>
      </div>

      <div className="clock__data d-flex align-item-center gap-4">
        <div className="text-center">
          <h1 className="text-white mb-2">{seconds}</h1>
          <h5 className="text-white">Seconds</h5>
        </div>
      </div>
    </div>
  );
}

export default Clock;
